import { React, useState } from 'react';

import { useContext } from 'react';
import { AdminContext } from '../../../../Panel';

import s from './AddReaderForm.module.css';
import sp from '../../../../../../assets/styles/popUp.module.css';
import f from '../../../../../../assets/styles/form.module.css';
import { ServicesContext } from '../../../../../..';

const AddReaderForm = props => {
	const Services = useContext(ServicesContext);
	const [reader, setReader] = useState({
		full_name: '',
		email: '',
		work_address: '',
		// birthday: '',
		home_address: '',
		phone_number: '',
		// passport: '',
	});

	return (
		<div className={s.container}>
			<div className={sp.header}>
				<span> </span>
				<button className={sp.closeBtn} onClick={props.close}>
					
				</button>
			</div>
			<div className={sp.content}>
				<form
					className={f.form}
					onSubmit={e => {
						e.preventDefault();
						console.log(reader);
						// Promise.resolve(
						//     Auth.AuthService.makeLogin({
						//         email: state.email,
						//         password: state.password,
						//     })
						// ).then(res => (res !== undefined ? (Auth.status = res ? 'librarian' : 'user') : Auth.status = 'anonym'));
					}}
				>
					<ul className={`${f.fieldsList} + ${sp.fields}`}>
						<li className={f.fieldBlock}>
							<label></label>
							<input
								type='text'
								value={reader.full_name}
								name='full_name'
								onChange={e =>
									setReader({
										...reader,
										full_name: e.target.value,
									})
								}
							/>
						</li>
						<li className={f.fieldBlock}>
							<label>Email</label>
							<input
								type='email'
								value={reader.email}
								name='email'
								onChange={e =>
									setReader({
										...reader,
										email: e.target.value,
									})
								}
							/>
						</li>
						<li className={f.fieldBlock}>
							<label> </label>
							<input
								type='text'
								value={reader.work}
								name='work'
								onChange={e =>
									setReader({
										...reader,
										work: e.target.value,
									})
								}
							/>
						</li>
						{/* <li className={f.fieldBlock}>
							<label> </label>
							<input
								type='daye'
								value={reader.birthday}
								placeholder={reader.birthday}
								name='birthday'
								onChange={e =>
									setReader({
										...reader,
										birthday: e.target.value,
									})
								}
							/>
						</li> */}
						<li className={f.fieldBlock}>
							<label> </label>
							<input
								type='text'
								value={reader.home_address}
								name='home_address'
								onChange={e =>
									setReader({
										...reader,
										home_address: e.target.value,
									})
								}
							/>
						</li>
						<li className={f.fieldBlock}>
							<label> ( )</label>
							<input
								type='text'
								value={reader.phone_number}
								name='phone_number'
								onChange={e =>
									setReader({
										...reader,
										phone_number: e.target.value,
									})
								}
							/>
						</li>
						{/* <li className={f.fieldBlock}>
							<label> ( )</label>
							<input
								type='text'
								value={reader.passport}
								name='passport'
								onChange={e =>
									setReader({
										...reader,
										passport: e.target.value,
									})
								}
							/>
						</li> */}
					</ul>
					<div className={f.btns}>
						<input
							className={f.btn}
							type='submit'
							value=' '
							onClick={e => {
								e.preventDefault();
								console.log(reader);
								Services.AdminService.AddReader(reader);
							}}
						/>
					</div>
				</form>
			</div>
		</div>
	);
};
export default AddReaderForm;
